
// Films Component Styles
.fm {
    width: 70%;
    margin-bottom: 30px;
    &_containerMain{ 
        background-color: white;
        width: 130px;
        height: 250px;
        padding: 10px;
        cursor: pointer;
        float: left;
        margin-right: 8px;
        margin-bottom: 20px;
        border-width: 1px;
        border-style: solid;
        border-color: rgba(46, 46, 46, 0.1);
        // animation: slide-down 300ms ease-out forwards;
    }
    &_poster {
        width: 100%;
        height: 85%;
        &--hidden {
            width: 100%;
            height: 85%;
            cursor: auto;
        }
    }
    &_description {
        white-space: nowrap; 
        overflow: hidden;
        text-overflow: ellipsis;
        width: 132px;
        height: 21px;
        font-size: 12.5px;
        // text-decoration: none;
        // text-decoration-skip-ink: none;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: 600;
    }
}

@keyframes slide-down {
    from {
      opacity: 0;
      transform: translateY(-3rem);
    }
    to {
      opacity: 1;
      transform: translateY(0rem);
    }
}

// Best Rated Component Styles
.sd {
    &_containerMain {
        width: 30%;
        float: right;
    }
    &_containerInd {
        display: flex;
        margin-bottom: 10px;
    }
    &_poster {
        float: left;
        width: 17%;
        height: 25%;
    }
    &_title {
        color: rgb(98, 98, 98);
        font-size: 16px;
        font-family: Arial, Helvetica, sans-serif;
    }
    &_description {
        width: 225px;
        height: 21px;
        font-size: 15px;
        padding-left: 8px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
    }
}

@media all and (max-width: 1024px) {
    .sd {
        &_containerMain {
            width: 100%;
            float: right;
        }
    }
    .fm {
        width: 100%;
        &_containerMain{ 
            background-color: rgb(255, 255, 255);
            width: 95%;
            height: auto;
        }
    }
}
