%LettlerStyle {
    color: grey;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
}

// Navbar Styles
.navbar {
    list-style: none;
    width: 50%;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    height: 30px;
    z-index: -1;
    &_stylelist {
        flex-direction: row;
    }
    &_img {
        float: left;
        width: 50%;
        // margin-right: 20px;
        // height: 135%;
    }
    &_iconMobile {
        display: none;
    }
    &_items {
        @extend %LettlerStyle;
        height: auto;
        width: 50px;
        margin-left: 40px;
        text-align: center;
        color: $morado;
        font-size: 14.8px;
        margin-top: 0px;
        float: right;
    }
    &_input {
        border-color: rgba(105, 18, 97, 0.3);
        margin-top: 5px;
        border-radius: 5px;
        width: 200px;
        height: 20px;
        margin-right: 80px;
        color: rgb(61, 61, 61);
    }
}

// Filter/Categories homepage
.ctg {
    &_Container {
        width: 100%;
        height: auto;
    }
    &_filter {
        display: flex;
        background-color: $morado;
        width: 100%;
        border-radius: 2px;
    }
    &_itemsWhite {
        @extend %LettlerStyle;
        margin-left: 20px;
        color: white;
    }
    &_itemsNormal {
        @extend %LettlerStyle;
        margin-left: 20px;
    }
    &_containerCategories {
        display: flex;
        background-color: whitesmoke;
        border-radius: 2px;
    }
}


@media all and (max-width: 1024px) {
    .navbar {
        list-style: none;
        width: 100vh;
        margin-right: auto;
        margin-left: auto;
        display: flex;
        height: 30px;
        z-index: -1;
        &_img {
            display: none;
            margin-right: 0px;
        }
        &_iconMobile {
            display: block;
            font-size: 30px;
            color: $morado;
            padding-left: 0px;
        }
        &_items {
            text-align: left;
            float: left;
            margin-left: 0px;
            margin-right: 0px;
        }
        &_input {
            width: 140px;
            height: 30px;
            margin-top: 0px;
            margin-left: 5px;
            margin-right: 10px;
        }
        .ctg {
            &_Container {
                width: 100vh;
                height: auto;
            }
        }
    }
}
    

