
%letterStyle {
    color: rgb(56, 56, 56);
    font-size: 17px;
    font-family: Arial, Helvetica, sans-serif;
}

%descriptionStyle {
    white-space: nowrap; 
    overflow: hidden;
    text-overflow: ellipsis;
}

.inf {
    &_containerMain {
        // background-color: aqua;
        display: flex;
        width: 100%;
    }
    &_containerInfo {
        // background-color: blue;
        width: 100%;
        padding: 5px;
    }
    &_profileImage {
        width: 250px;
        border-radius: 5px;
    }
    &_text {
        &--normal {
            @extend %letterStyle;
            width: 100%;
        }
        &--bold {
            @extend %letterStyle;
            font-weight: bold;
            margin-bottom: 0px;
            width: 100%;
        }
        &--title {
            @extend %letterStyle;
            font-weight: bold;
            font-size: 18px;
            width: 100%;
        }
    }
}

.bio {
    &_containerMain {
        @extend %descriptionStyle;
        white-space: normal; 
        width: 97%;
        height: 375px;
        text-align: left;
        float: left;
        padding: 5px;
        margin-bottom: 40px;
    }
    &_text {
        @extend %letterStyle;
        &--title {
            font-size: 25px;
            font-weight: bold;
            margin-top: 0px;
        }
    }
}

// Media Component Style
.med {
    &_containerMain {
        display: flex;
        width: 97%;
        height: 220px;
        float: left;
        padding: 5px;
    }
    &_containerImages {
        width: 150px;
        height: 220px;
        margin-left: 10px;
    }
    &_image {
        width: 100%;
        height: 100%;
        border-radius: 5px;
    }
}

@media all and (max-width: 1024px) {
    .inf {
        &_containerMain {
            display: grid;
            width: 100%;
        }
        &_containerInfo {
            width: 100%;
            padding: 5px;
        }
        &_profileImage {
            width: 96%;
            border-radius: 5px;
        }
    }
    .bio {
        &_containerMain {
            width: 100%;
            height: auto;
        }
    }
    .med {
        &_containerMain {
            display: grid;
            width: 95%;
            height: 130rem;
        }
        &_containerImages {
            width:100%;
            height: auto;
            margin-left: 10px;
        }
        &_image {
            width: 100%;
            height: 100%;
            border-radius: 5px;
        }
    }
}