body {
    background-color: rgb(245, 245, 245);
}

nav {
    position: fixed;
    background-color: #ffffff;
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.2);
    width: 100%;
    top: 0rem;
    left: 0rem;
}

@media all and (max-width: 1024px) { 
    nav {
        width: 100vh;
    }
}

a {
    color: rgb(98, 98, 98);
    text-decoration: none;
}